body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  font: 0.875em "Century Gothic", Futura, sans-serif;
  background: aliceblue;
}

ol, ul {
  padding-left: 1.875em;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#loadOverlay{display: none;}
.btn-circle {
    line-height: 1.428571429;
    border-radius: 0.27em;
}

.box-connect-four {
    border: 5px solid darkblue;
    float: left;
    font-size: 54px;
    font-weight: bold;
    line-height: 34px;
    height: 64px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 64px;
    background: transparent;
}

.box-connect-four:focus {
    outline: none;
}

.kbd-navigation .box-connect-four:focus {
    background: #ddd;
}

.game {
    margin-left: 45em;
    margin-top: 12em;
}

.body {
    margin-top: 5em;
}

.pagetitle {
    margin-left: 6em;
    margin-top: -9em;
}

.btn-group-nextprevious {
    margin-left: 13.5em;
}
.status {
    margin-top: 3em;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.box-tic-tac {
    float: left;
    font-size: 4em;
    font-weight: bold;
    line-height: 0.6em;
    height: 1.2em;
    margin-right: -0.02em;
    margin-top: -0.02em;
    padding: 0;
    text-align: center;
    width: 1.2em;
    color: white;
    background: transparent;
}



.kbd-navigation .box:focus {
    background: #ddd;
}

.game-tic-tac {
    margin-top: 10em;
}

.blackboard {
    margin-left: -30em;
    margin-right: 10em;
    margin-top: -10em;
    background: black;
    border: 10px solid #5b3a29;
}

.tic-tac-buttons {
    margin-top: 1.1625em;
    margin-bottom: 3em;
}

.body-tic-tac {
    color: white;
    margin-left: 41em;
    margin-top: 10.5em;
    font-family: "Comic Sans MS";
}

.pagetitle-tic-tac {
    margin-top: 1em;
    margin-left: 38em;
    color: white;
    font-family: "Comic Sans MS";
}

.squarenumber input{
    background: transparent;
    color: white;
    margin-left: 2em;
}
.squarenumber {
    margin-left: -2em;
}

.personal-outline-danger {
    color: red;
    background: transparent;
    border: 0.1em solid red;
}

.personal-outline-primary {
    color: #157DEC;
    background: transparent;
    border: 0.1em solid #157DEC;
}

.personal-outline-danger:hover {
    color: red;
}

.personal-outline-primary:hover {
    color: #157DEC;
}

.box-tic-tac:focus {
    outline: none;
    border-style: outset;
}
